export default {
    calc(javob, togri) {
        return Object.keys(togri).map((v, n) => {
            return togri[v] === javob[n]
        })
    },
    calcNatija(n) {
        let result = 0
        n.forEach(value => {
            value && result++
        })
        return Math.round((result / n.length * 100)*10)/10
    },
    sort(data) {

        data.sort((v, b) => {
            if (b.result === v.result) {
                if (v.vaqt.hour === b.vaqt.hour) {
                    if (v.vaqt.minut === b.vaqt.minut) {
                        if (v.vaqt.sec === b.vaqt.sec) {
                            return v.vaqt.mil - b.vaqt.mil
                        } else {
                            return v.vaqt.sec - b.vaqt.sec
                        }

                    } else {
                        return v.vaqt.minut - b.vaqt.minut
                    }
                } else {
                    return v.vaqt.hour === b.vaqt.hour
                }
            } else {
                return b.result - v.result
            }
        })

        if (data.length >= 10 ){
            data = data.pop()
        }
        return data
    }
}