import React, {useEffect, useState} from 'react'
import {NavLink, Redirect} from 'react-router-dom'

import {images} from '../static/savollar'
import {savollar} from '../static/savollar'
import {javoblar} from '../static/savollar'
import {variyantlar} from '../static/savollar'
import './viktorina.css'


const Start = (props) => {
    const top = Object.keys(savollar)
    let answer = []
    for (const i in top) {
        answer.push('')
    }
    const [page, setPage] = useState(0)
    const [show, setShow] = useState('none')

    const [javob1, setJavob1] = useState(answer)

    const setJavob = (n, value) => {

        let newpage
        if (page === top.length-1) {
            newpage = 0
        } else {
            newpage = page + 1
        }
        setPage(newpage)
        answer = javob1
        answer[n] = value
        setJavob1(answer)

    }
    const span = top.map((i, count) => {
        if (count === page) {
            return <span className='dot active'></span>
        }
        return <span onClick={() => {
            setPage(count)
        }} className='dot'></span>
    })


    const compareRandom = (a, b) => {
        return Math.random() - 0.5;
    }

    const viktorina = top.map(
        (sav, count) => {
            return <div style={{backgroundImage: `url(${images[sav]})`}} className={`savollar`}>
                <div className='test'>
                    <div className="numbertext">{`${count + 1}/${top.length}`} </div>
                    <div className={'savol'}>{savollar[sav]}</div>

                    <ul>{variyantlar[sav].sort(compareRandom).map(variant => <li className={`variant${count + 1}`}
                                                                                 onClick={() => {
                                                                                     setJavob(count, variant)
                                                                                 }}>{variant}</li>)}</ul>
                </div>
            </div>
        }
    )

    return <div>{
        !props.start ? <Redirect to='/'/>
            :<div >
                {viktorina[page]}
                <div>
                    <div className='zzz'>
                        {span}
                    </div>
                    <div>
                <span onClick={() => {
                    let newpage = (page === 0) ? top.length-1 : page - 1
                    setPage(newpage)
                }}>{'<'}</span>
                        <span onClick={() => {
                            let newpage = (page === top.length-1) ? 0 : page + 1
                            setPage(newpage)
                        }}>{'>'}</span>
                    </div>
                    <div>
                        <button className='showAns' onClick={() => {
                            setShow('javoblar')
                        }}>Javoblarim
                        </button>
                        <div className={show}>
                            <div className={'close'} onClick={() => {
                                setShow('none')
                            }}>x
                            </div>
                            <ul>{javob1.map(value =><li>{value || "javob belgilanmadi"}</li> )}

                            </ul>
                        </div>
                    </div>
                </div>
                    <button onClick={() => {
                        props.stopSet(new Date().getTime(),javob1)
                    }} className="StopButton">
                        Yakunlash
                    </button>


            </div>
    }</div>

}

export default Start