import React from 'react'
import {NavLink, Redirect} from "react-router-dom";

export default (props) => {

    return (
        <div>{
            ( !props.table) ? <div>{alert('Jadval bosh')} <Redirect to='/'/></div>
                :
                <div className='list'>
                    <div>Liderlar</div>
                    <table border={'1'}>
                        <tr>
                            <th>â„–</th>
                            <th>ISMI</th>
                            <th>NATIJA</th>
                            <th>VAQTI</th>
                        </tr>
                        {props.table.map((v, n) => <tr>
                            <th>{n+1}</th>
                            <td>{v.name}</td>
                            <td>{v.result}</td>
                            <td>{`${v.vaqt.hour|| 0}:${v.vaqt.min||0}:${v.vaqt.sec || 0}:${v.vaqt.mil || 0}`}</td>
                        </tr>)}
                    </table>

                    <NavLink to='/'>
                        <button className='pre'>Home</button>
                    </NavLink>
                </div>

        }
        </div>
    )

}