import React from "react";
import "./pre.css";
import {NavLink, Redirect} from "react-router-dom";


const Natija = (props) => {
    console.log(props.user)
    return <div>{
        ( !props.user) ? <Redirect to='/'/>
        :
            <div className='natija'>
                <div>{props.user.name}</div>
                <ul>
                    {props.user.natija.map((v)=> <li>{ v ? "To'g'ri " : "Notog'ri"}</li> )}
                    <div>

                        <p>{` Sizning vaqtingiz:  ${props.user.vaqt.hours}:${props.user.vaqt.minut}:${props.user.vaqt.sec}:${props.user.vaqt.mil} `}</p>
                        <p>{`sizning natijangiz ${props.user.result}%`}</p>

                    </div>
                </ul>
                <NavLink to='/'>
                    <button className='pre'>Home</button>
                </NavLink>
            </div>

    }
    </div>
}

export default Natija