import React, {useEffect, useState, useMemo} from "react";
import './App.css'
import secundomer from './utilitlar/sekundomer'
import Start from "./componentlar/viktorina_start";
import {NavLink, useHistory, BrowserRouter, Route} from 'react-router-dom'
import Natija from "./componentlar/Natija";
import {javoblar} from './static/savollar';
import { withRouter } from 'react-router'
import calculator from "./utilitlar/calc";
import Lider from "./componentlar/Lider";

function App(props) {
    const name = React.createRef()
    const [start, setStart] = useState(0)
    const [vaqt, setVaqt] = useState()
    const [javob, setJavob] = useState()
    const [natija, setNatija] = useState()
    const [user, setUser] = useState()
    const [nam, setNam] = useState()
    const [table, setTable] = useState([])

    const userName = () => {
        let value = name.current.value || 'guest'
        setNam(value)
    }
    useEffect(()=>{
        natija && setUser({
            name: nam,
            vaqt,
            natija,
            result: calculator.calcNatija(natija)
        })

    },[natija])
    useEffect(()=>{
        let data = table
        user && data.push(user)
        data = calculator.sort(data)
        setTable(data)
        user && props.history.push('/natija')
    },[user])

    const setStop = async (t, v) => {
        setJavob(v)
        setNatija(calculator.calc(v, javoblar))
        const time = secundomer(start, t)
        setVaqt(time)




    }

    return (

            <div className={'container'}>
                <div className="navbar">Geografiyadan viktorina</div>
                <div className='Container'>
                    <Route path='/natija' component={() => <Natija javoblar={javoblar} user={user}/>}/>
                    <Route path='/list' component={() => <Lider table={table} />}/>
                    <Route path='/viktorina'
                           component={() => <Start start={start}  stopSet={setStop}/>}/>
                    <Route exact path='/' component={() => {
                        return <div className="StartButton">
                            <input type="text" placeholder=" ismingizni kiriting" ref={name}/>
                            <NavLink to={'/viktorina'}>
                                <button onClick={() => {
                                    setStart(new Date().getTime())
                                    userName()
                                }}> Start
                                </button>
                            </NavLink>
                            <NavLink to='/list' ><div className='lider'>Liderlar</div></NavLink>
                        </div>
                    }}/>
                </div>


            </div>

    )
}

export default withRouter(App);
